body {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 16px;
}

.container-collapse {
    box-sizing: border-box;
    width: 100vw;
    display: flex;
    flex-direction: column;
    padding: 20px;
    background-color: white;
}

.btn-collapse {
    color: white;
    background-color: rgb(36, 67, 223);
    border: none;
    border-radius: 5px;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1rem;
    width: 100px;
    height: 40px;
    margin-bottom: 20px;
    cursor: pointer;
}

.btn-collapse:hover {
    outline: 3px solid rgb(58, 180, 255);
    outline-offset: 1px;
}

.collapse-area {
    width: 100%;
    overflow: hidden; 
    height: auto; 
}

.collapse-text {
    box-sizing: border-box;
    padding: 15px;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1rem;
    line-height: 1.3rem;
    resize: none;
    width: 98%;
    border: solid 1px rgb(182, 182, 182);
    border-radius: 5px;
    height: 100px; 

}

.copy {
    text-align: right;
    color: grey;
    font-size: 0.8rem;
    margin-right: 30px;

}

@keyframes collapse {
    0% {
        height: 100px;
    }
    100% {
        height: 0px;
    }
}

@keyframes expand {
    0% {
        height: 0px;
    }
    100% {
        height: 100px;
    }
}

.collapsing {
    animation: collapse 2s forwards;
}

.expanding {
    animation: expand 2s forwards;
}

.container-like {
    box-sizing: border-box;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: end;
    padding: 0px 40px;
    height: 250px;
    position: relative;
}

.like-btn {
    color: white;
    background-color: rgb(223, 36, 36);
    border: none;
    border-radius: 5px;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.1rem;
    width: 100px;
    height: 40px;
    margin-bottom: 20px;
    cursor: pointer;
}

.heart {
    position: absolute;
    bottom: 40px;
    width: 40px;
    height: 40px;
    background-image: url('../img/heart.png'); 
    background-size: cover;
    animation-duration: 500ms;
    animation-timing-function: ease-out;
    animation-fill-mode: forwards;
}

@keyframes trajectory1 {
    0% { transform: translateX(0) translateY(0); }
    25% { transform: translateX(-50px) translateY(-50px); }
    50% { transform: translateX(0) translateY(-100px); }
    75% { transform: translateX(50px) translateY(-150px); }
    100% { transform: translateX(0) translateY(-200px); }
}

@keyframes trajectory2 {
    0% { transform: translateX(0) translateY(0); }
    25% { transform: translateX(0) translateY(-50px); }
    50% { transform: translateX(50px) translateY(-100px); }
    75% { transform: translateX(-50px) translateY(-150px); }
    100% { transform: translateX(0) translateY(-200px); }
}

@keyframes trajectory3 {
    0% { transform: translateX(0) translateY(0); }
    25% { transform: translateX(0) translateY(-50px); }
    50% { transform: translateX(-50px) translateY(-100px); }
    75% { transform: translateX(50px) translateY(-150px); }
    100% { transform: translateX(0) translateY(-200px); }
}

@keyframes trajectory4 {
    0% { transform: translateX(0) translateY(0); }
    25% { transform: translateX(50px) translateY(-50px); }
    50% { transform: translateX(0) translateY(-100px); }
    75% { transform: translateX(-50px) translateY(-150px); }
    100% { transform: translateX(0) translateY(-200px); }
}

.container-chat {
    box-sizing: border-box;
    width: 100vw;
    display: flex;
    justify-content: end;
    align-items: end;
    padding: 0px 40px;
    margin-bottom: 30px;
    height: 300px;
}

.chat-open-btn {
    background-color: rgb(226, 23, 23);
    border: none;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    position: relative;;

    cursor: pointer;
    display: flex;
}

.chat-open-btn:hover {
    width: 60px;
    height: 60px;
    top: 5px;
    right: -5px;
}

.chat-window {
    display: none;
    flex-direction: column;
    border: solid 1px black;
    border-radius: 5px;
    height: 250px;
    width: 250px;
    padding: 10px;
    padding-top: 5px;
    position: relative;
}

.chat-title {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1rem;
}

.chat-close-btn {
    background: none;
    border: none;
    font-size: 1.5rem; 
    cursor: pointer;
    position: absolute;
    top: 5px;
    left: 236px;  
}

.chat-close-btn:hover {
    font-size: 1.8rem; 
    top: 2px;
    left: 235px;
}

.chat-text {
    box-sizing: border-box;
    padding: 15px;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1rem;
    line-height: 1.3rem;
    width: 99%;
    border: solid 1px rgb(182, 182, 182);
    border-radius: 5px;
    height: 200px; 
    margin-bottom: 5px;
    resize: none;
}

.chat-submit-btn {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 0.9rem;
    background-color: rgb(206, 202, 202);
    color: rgb(19, 18, 18);
    border: solid 1px grey;
    border-radius: 5px;
    width: 99%;
    height: 35px;
    cursor: pointer;

}

.chat-submit-btn:hover {
    outline: 2px solid rgb(171, 175, 177);
    outline-offset: 1px;
}